// System CSS
// import "react-datepicker/dist/react-datepicker.css";

// System imports
import React, { useState, useEffect } from "react";
import { useTranslation, Trans } from "react-i18next";
import { Form, InputGroup, Alert } from "react-bootstrap";
// import { Redirect } from "react-router-dom";
// import ChevronRight from "@material-ui/icons/ArrowForwardIos";
import { KeyboardArrowRight } from "@material-ui/icons";
import moment from "moment-timezone";
// import DatePicker from "react-datepicker";
import { Today } from "@material-ui/icons";
// import { Today } from "@material-ui/icons";
// import DatePicker from "react-date-picker";
// import MomentUtils from "@date-io/moment";
// import { MuiPickersUtilsProvider } from "@material-ui/pickers";
// import { DatePicker, MuiPickersUtilsProvider } from "@material-ui/pickers";
import _ from "lodash";

// User imports
import Card from "../card";
import { Formik } from "formik";
import * as yup from "yup";
// import { useStickyState } from "../../utils/state";
import Dropzone from "../dropzone";
import SubmitButton from "../submitButton";
import ModalMessage from "../modalMessage";
import { useStickyState } from "../../utils/state";
import timezones from "./timezones.en.json";

// const UserAgreement = React.lazy(() => import("../locale/en/UserAgreement"));

export default (args) => {
  // console.log("ACCOUNT REGISTRATION ARGS:", args);
  /**
   * Default Properties
   */
  let defaults = {
    request: "get user sequence", // "register account",
    namespace: "AccountRegistration",
  };

  /**
   * Properties
   */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   */
  const {
    signedIn,
    accountNumber,
    accountPhotoPath,
    userData,
    language,
    debug,
  } = props.barrelGetters;
  // const {} = props.barrelSetters;
  const {
    getReviser,
    getWorkplace,
    sendRequest,
    formatDate,
  } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
   * Hooks
   */
  // const [redirect, setRedirect] = useState(false);
  const [userAgreementShow, setUserAgreementShow] = useStickyState(
    false,
    "userAgreementShow"
  );
  const [privacyPolicyShow, setPrivacyPolicyShow] = useStickyState(
    false,
    "privacyPolicyShow"
  );

  const [offsetList, setOffsetList] = useStickyState(null, "offsetList");
  const [calenarVisible, setCalenarVisible] = useState(false);
  const [startDate, setStartDate] = useState(Date.now());

  /**
   * Custom hooks
   */
  const { t } = useTranslation(props.namespace);

  /**
   * Yup schema
   */
  const schema = yup.object({
    dropzoneHook: yup.string().required(t("Photo ID is required")),
    terms: yup.array().required(t("Terms is required")),
    timezone: yup.string().required(t("Timezone at birth is required")),
    birthDate: yup
      // .date()
      .string()
      .required(t("Date of birth is required"))
      // .max(new Date(), t("Date of birth must be today or earlier"))
      // .string()
      // .matches(new RegExp(t("Date Regex")), t("Wrong date format"))
      .test({
        name: "regex",
        exclusive: true,
        params: {},
        message: t("Date must be valid"),
        test: (value) => {
          if (typeof value === "undefined") return false;

          // Test regex
          if (!RegExp(t("Date Regex")).test(value)) return false;

          const date2en = formatDate(
            value,
            { month: "numeric", day: "numeric", year: "numeric" },
            false,
            "en-US"
          );

          return _.isDate(new Date(date2en));
        },
      }),
    // personalID: yup
    //   .string()
    //   .optional() // .required(t("Personal ID is required"))
    //   .matches(
    //     new RegExp(t("Personal ID Regex")),
    //     t("Wrong Personal ID format")
    //   )
    //   .test({
    //     name: "integrity",
    //     exclusive: true,
    //     params: {},
    //     message: t("PersonalID must be valid"),
    //     test: (value) => {
    //       if (typeof value === "undefined") return false;
    //       let pid = value
    //         .replace("-", "")
    //         .split("")
    //         .map(function (n) {
    //           return parseInt(n, 10);
    //         });
    //       let calc =
    //         1 * pid[0] +
    //         6 * pid[1] +
    //         3 * pid[2] +
    //         7 * pid[3] +
    //         9 * pid[4] +
    //         10 * pid[5] +
    //         5 * pid[6] +
    //         8 * pid[7] +
    //         4 * pid[8] +
    //         2 * pid[9];
    //       let checksum = (1101 - calc) % 11;

    //       return checksum === pid[10];
    //     },
    //   }),
  });

  /**
   * Functions
   */

  /**
   * Send account registration request
   * @param {object} data
   */
  const sendAccountRegistrationRequest = (data) => {
    if (data === null) return false;

    data.reviser = getReviser();
    data.workplace = getWorkplace();
    data.email = userData.email;

    return sendRequest({ req: props.request, data });
  };

  /**
   * Event listener: On Form Submit
   * @param {object} data Form data
   */
  const onSubmit = (data) => {
    if (sendAccountRegistrationRequest(data)) {
      // Account registration request was submitted
    }
  };

  // const timezones = 1;

  const timezones_sorted = timezones.sort((a, b) =>
    (a.Offset + 12) % 24 > (b.Offset + 12) % 24
      ? 1
      : (a.Offset + 12) % 24 === (b.Offset + 12) % 24
      ? a.Name > b.Name
        ? 1
        : -1
      : -1
  );

  const handleCalendarChange = (e) => {
    console.log("handleCalendarChange", e);
  };

  // console.log(timezones_sorted);

  // console.log(map);

  // tzNames.forEach(
  //   (value, key) => (timezoneList += <option value={key}>{value}</option>)
  // );
  // console.log(timezoneList);

  let timeZoneID = 0;
  let prevTimeZoneID = 0;

  const AccountRegistrationForm = ({
    handleSubmit,
    handleChange,
    handleBlur,
    values,
    touched,
    isValid,
    validateForm,
    setFieldValue,
    errors,
  }) => (
    <Form noValidate onSubmit={handleSubmit}>
      <Form.Group controlId="validationFormikPhotoId">
        <Form.Label>{t("Photo ID")}</Form.Label>

        <Form.Text>
          <Trans i18nKey="AccountRegistration:Photo ID requirements">
            Photo of me holding my passport. The personal identification number
            must be clearly legible.
          </Trans>
        </Form.Text>

        <br />
        <Dropzone data={props} />
      </Form.Group>

      <Form.Group controlId="validationFormikDropzoneHook">
        <Form.Control
          type="text"
          name="dropzoneHook"
          value={accountPhotoPath}
          onChange={handleChange}
          isInvalid={!!errors.dropzoneHook}
          style={{ display: "none" }}
        />

        <Form.Control.Feedback type="invalid">
          {errors.dropzoneHook}
        </Form.Control.Feedback>
      </Form.Group>

      {false && (
        <Form.Group controlId="validationFormikCountry">
          <Form.Label>{t("Country")}</Form.Label>
          <Form.Control as="select" custom>
            <option>Latvija</option>
            <option>Deutschland</option>
          </Form.Control>
        </Form.Group>
      )}

      <Form.Group controlId="validationFormikBirthDate">
        <Form.Label>{t("Date of birth")}</Form.Label>
        <InputGroup>
          <Form.Control
            type="text"
            placeholder={t("yyyy-mm-dd")}
            aria-describedby="inputGroupPrepend"
            name="birthDate"
            value={values.birthDate}
            onChange={handleChange}
            isInvalid={!!errors.birthDate && touched.birthDate}
          />

          <Form.Control.Feedback type="invalid">
            {errors.birthDate}
          </Form.Control.Feedback>
        </InputGroup>
      </Form.Group>

      <Form.Group controlId="validationFormikTimezone">
        <Form.Label>{t("Timezone at birth")}</Form.Label>
        <Form.Control
          as="select"
          custom
          name="timezone"
          value={values.timezone}
          onChange={handleChange}
          isInvalid={!!errors.timezone && touched.timezone}
        >
          <option value="">{t("Choose...")}</option>
          {timezones_sorted.map((element, key) => (
            <option
              key={key}
              value={`${(((element.Offset + 12) % 24) + 1)
                .toString()
                .padStart(2, "0")} - ${element.Name}`}
            >
              {(((element.Offset + 12) % 24) + 1).toString().padStart(2, "0")} -{" "}
              {element.Name}
            </option>
          ))}
        </Form.Control>
        <Form.Control.Feedback type="invalid">
          {errors.timezone}
        </Form.Control.Feedback>
      </Form.Group>

      {false && (
        <Form.Group controlId="validationFormikPersonalId">
          <Form.Label>{t("Personal ID")}</Form.Label>
          <InputGroup>
            <InputGroup.Prepend>
              <InputGroup.Text id="inputGroupPrepend">ID</InputGroup.Text>
            </InputGroup.Prepend>
            <Form.Control
              type="text"
              placeholder={t("XXXXXX-XXXXX")}
              aria-describedby="inputGroupPrepend"
              name="personalID"
              value={values.personalID}
              onChange={handleChange}
              isInvalid={!!errors.personalID && touched.personalID}
            />
            <Form.Control.Feedback type="invalid">
              {errors.personalID}
            </Form.Control.Feedback>
          </InputGroup>
        </Form.Group>
      )}

      <Form.Group>
        <Form.Check
          required
          name="terms"
          label={t("Agree to terms and conditions")}
          onChange={handleChange}
          isInvalid={!!errors.terms && touched.terms}
          feedback={errors.terms}
          id="validationFormik0"
        />
      </Form.Group>

      <Trans i18nKey="AccountRegistration:The User Agreement and the Privacy Policy">
        <KeyboardArrowRight style={{ color: "var(--dark)" }} />
        <a href="#" onMouseDown={() => setUserAgreementShow(true)}>
          User Agreement
        </a>

        <br />

        <KeyboardArrowRight style={{ color: "var(--dark)" }} />
        <a href="#" onMouseDown={() => setPrivacyPolicyShow(true)}>
          Privacy Policy
        </a>
      </Trans>

      <br />
      <br />

      <SubmitButton
        {...args}
        caption={t("Submit")}
        request={props.request}
        namespace={props.namespace}
      />
    </Form>
  );

  if (signedIn === false)
    return (
      <>
        <Alert variant="info">
          <Alert.Heading>{t("User not logged in")}</Alert.Heading>
          <p>
            <Trans i18nKey="AccountRegistration:Please log in first">
              Please log in first.
            </Trans>
          </p>
        </Alert>
      </>
    );

  if (!debug && accountNumber !== null)
    return (
      <>
        <Alert variant="info">
          <Alert.Heading>{t("Account successfully registered")}</Alert.Heading>
          <p>
            <Trans
              i18nKey="AccountRegistration:Account with your personal ID successfully registered"
              accountNumber={accountNumber}
            >
              Account with the personal ID
              <strong>{{ accountNumber }}</strong>
              successfully registered.
            </Trans>
          </p>
        </Alert>
      </>
    );

  return (
    <>
      <ModalMessage
        show={userAgreementShow}
        file="en/UserAgreement"
        size="lg"
        centered={false}
        title={t("User Agreement")}
        footer=""
        onHide={() => setUserAgreementShow(false)}
      />

      <ModalMessage
        show={privacyPolicyShow}
        file="en/PrivacyPolicy"
        size="lg"
        centered={false}
        title={t("Privacy Policy")}
        footer=""
        onHide={() => setPrivacyPolicyShow(false)}
      />

      <Card title={t("Open account")}>
        <Formik
          component={AccountRegistrationForm}
          validationSchema={schema}
          onSubmit={onSubmit}
          initialValues={{
            // personalID: "",
            terms: "",
            dropzoneHook: accountPhotoPath,
            timezone: "",
            birthDate: "",
          }}
        />
      </Card>
    </>
  );
};
